import React from 'react';
import{
    Button,
    Form,
    FormControl,
    Nav, 
    Navbar,
    NavDropdown,
} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {link, NavLink, withRouter} from 'react-router-dom';
import {
    HomeRounded,
    SchoolRounded,
    WorkRounded,
    Facebook,
    GitHub,
    Telegram,
} from '@material-ui/icons';
import resumeDate from '../../utils/resumeDate';
import CustomBtn from '../Button/Button';
import './Header.css'; 

const Header = (props) => {
    const pathName = props?.location?.pathname;

    return (
        <Navbar bg="light" expand="lg" sticky='top' className='header'>
            {/* Home link */}
            <Nav.Link as={NavLink} to ='/'>
                <Navbar.Brand className='header_home'>
                    <HomeRounded/>
                </Navbar.Brand>
            </Nav.Link>

            <Navbar.Toggle/>

            <Navbar.Collapse>
                <Nav className='header_left'>
                    {/* Resume Link */}
                    <Nav.Link
                        as={NavLink}
                        to='/resume'
                        className={pathName == '/' ? 'header_link_active' : 'header_link'}>
                        Резюме
                    </Nav.Link> 
                    {/* Portfolio Link */}
                    <Nav.Link
                        as={NavLink}
                        to='/portfolio'
                        className={pathName == '/portfolio' ? 'header_link_active' : 'header_link'}>
                        Портфолио
                    </Nav.Link> 
                </Nav>

                <div className='header_right'>
                    {Object.keys(resumeDate.socials).map(key => (
                        <a href={resumeDate.socials[key].link} target='_blank'>
                            {resumeDate.socials[key].icon}
                        </a>
                    ))}
                    <CustomBtn text={'Мой телеграм'} icon={<Telegram/>} link={'https://t.me/DmitryChistiakov'}/>
                </div>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default Header;