import { Typography } from '@material-ui/core';
import React from 'react';
import CustomTimeline, { CustomTimelineSeparator } from '../Timeline/Timeline';
import PersonOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined';

import resumeDate from '../../utils/resumeDate';
import ava from '../../assets/images/ava.jpg';
import './Profile.css';
import { TimelineContent, TimelineItem } from '@material-ui/lab';
import CustomBtn from '../Button/Button';
import GetAppIcon from '@material-ui/icons/GetApp';

const CustomTimelineItem = ({title, text, link}) => (
    <TimelineItem>
        <CustomTimelineSeparator/>
        <TimelineContent className='timeline_content'>

            {link ? (<Typography className='timelineitem_text'>
                <span>{title}: </span><a href={link} target='_blank'>{text}</a>
            </Typography>) :(
                <Typography className='timelineitem_text'><span>{title}:</span> {text}</Typography>
            )}
        </TimelineContent>
    </TimelineItem>
);

const CustomTimelineItemLink = ({title, text, link}) => (
    <TimelineItem>
        <CustomTimelineSeparator/>
        <TimelineContent className='timeline_content'>

            {link ? (<Typography className='timelineitem_text'>
                <a href={link} target='_blank'>{text}</a>
            </Typography>) :(
                <Typography className='timelineitem_text'><span>{title}:</span> {text}</Typography>
            )}
        </TimelineContent>
    </TimelineItem>
);

const Profile = () => {
    return (
        <div className='profile container_shadow'>
            <div className='profile_name'>
                <Typography className='name'>{resumeDate.name}</Typography>
                <Typography className='title'>{resumeDate.title}</Typography>
            </div>

            <figure className='profile_image'>
                <img src={ava} alt=""/>
            </figure>

            <div className='profile_information'>
                <CustomTimeline icon={<PersonOutlinedIcon/>}>
                    <CustomTimelineItem title='Имя' text={resumeDate.name} />
                    <CustomTimelineItem title='Профессия' text={resumeDate.title} />
                    <CustomTimelineItem title='Email' text={resumeDate.email} />
                
                    {Object.keys(resumeDate.socials).map(key => (
                        <CustomTimelineItemLink text={resumeDate.socials[key].text} link={resumeDate.socials[key].link}/>
                    ))}
                </CustomTimeline>
                <div className='button_container'>
                    <CustomBtn text={'Скачать PDF'} icon={<GetAppIcon/>} link='/portfolio/output.pdf'/>
                </div> 
            </div>
        </div>
    )
}

export default Profile