import { Grid, Icon, TextField, Typography } from '@material-ui/core';
import React from 'react';
import './Resume.css';
import resumeDate from '../../utils/resumeDate';
import CustomTimeline, { CustomTimelineSeparator } from '../../components/Timeline/Timeline';
import WorkIcon from '@material-ui/icons/Work';
import SchoolIcon from '@material-ui/icons/School';
import { TimelineConnector, TimelineContent, TimelineDot, TimelineItem, TimelineSeparator } from '@material-ui/lab';
import Paper from '@material-ui/core/es/Paper/Paper';
import CustomBtn from '../../components/Button/Button';

const Resume = () => {
    return (
        <>
            {/* About me */}
            <Grid container className='section pb_45 pt_45'>
                <Grid item className='section_title mb_30'>
                    <span></span>
                    <h6 className='section_title_text'>Обо мне</h6>
                </Grid>
                <Grid item xs={12}>
                    <Typography variant='body2' className='aboutme_text'>
                        {resumeDate.about}
                    </Typography>
                </Grid>
            </Grid>

             {/* Education and experiences */}       
            <Grid container className='section pb_45'>
                <Grid item className='section_title mb_30'>
                    <span></span>
                    <h6 className='section_title_text'>Резюме</h6>
                </Grid>

                <Grid item xs={12}>
                    <Grid container className='resume_timeline'>
                        {/* Education */}
                        <Grid item={12} md={6}>
                            <CustomTimeline title='Опыт работы' icon={<WorkIcon/>}>
                                {resumeDate.experiences.map(experiences =>(
                                    <TimelineItem>
                                        <TimelineSeparator className='separator_padding'>
                                            <TimelineDot  variant='outline' className='timeline_dot'/>
                                            <TimelineConnector/>
                                        </TimelineSeparator>
                                        <TimelineContent>
                                            <Typography className='timeline_title'>{experiences.title}</Typography>
                                            <Typography variant='caption' className='timeline_date'>{experiences.date}</Typography>
                                            <Typography variant='body2' className='timeline_description'>{experiences.description}</Typography>
                                        </TimelineContent>
                                    </TimelineItem>
                                ))}
                            </CustomTimeline>
                        </Grid>

                        {/* Experiences */}
                        <Grid item={12} md={6}>
                            <CustomTimeline title='Образование' icon={<SchoolIcon/>}>
                                {resumeDate.educations.map(education =>(
                                    <TimelineItem>
                                        <TimelineSeparator className='separator_padding'>
                                            <TimelineDot  variant='outline' className='timeline_dot'/>
                                            <TimelineConnector/>
                                        </TimelineSeparator>
                                        <TimelineContent>
                                            <Typography className='timeline_title'>{education.title}</Typography>
                                            <Typography variant='caption' className='timeline_date'>{education.date}</Typography>
                                            <Typography variant='body2' className='timeline_description'>{education.description}</Typography>
                                        </TimelineContent>
                                    </TimelineItem>
                                ))}
                            </CustomTimeline>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>

            {/* Services */}               
            <Grid container className='section pb_45'>
                <Grid item className='section_title mb_30'>
                    <span></span>
                    <h6 className='section_title_text'>Мои услуги</h6>
                </Grid>
                
                <Grid item xs={12}>
                    <Grid container spacing={3} justifyContent={'space-between'}>
                        {resumeDate.services.map(services =>(
                            <Grid item xs={12} sm={6} md={3}>
                                <div className='service'>
                                    <Icon className='service_icon'>{services.icon}</Icon>
                                    <Typography className='service_title' variant='h6'>{services.title}</Typography>
                                    <Typography className='service_description' variant='body2'>{services.description}</Typography>
                                </div>
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
            </Grid>

             {/* Skills */}               
            <Grid container className='section graybg pb_45 p_50'>
                <Grid item className='section_title mb_30'>
                    <span></span>
                    <h6 className='section_title_text'>Навыки</h6>
                </Grid>
                <Grid item xs={12}>
                    <Grid container  justifyContent={'space-between'} spacing={3}>
                        {resumeDate.skills.map(skill => (
                            <Grid item xs={12} sm={6} md={3}>
                                <Paper elevation={0} className='skill'>
                                    <Typography variant='h6' className='skills_title'>
                                        {skill.title}
                                    </Typography>
                                    {skill.describe.map(element => (
                                        <Typography variant='body2' className='skill_description'>
                                            <TimelineDot variant='outlined'  className='timeline_dot'/>
                                            {element}
                                        </Typography>  
                                    ))}
                                </Paper>
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
            </Grid>

            {/* Contacts */}               
            <Grid container spacing={6} className='section pt_45 pb_45'>
                {/* Contacts form */}
                <Grid item xs={12} lg={7}>
                    <Grid container>
                        <Grid item className='section_title mb_30'>
                            <span></span>
                            <h6 className='section_title_text'>Контактная форма</h6>
                        </Grid>
                        <Grid item xs={12}>
                            <Grid container spacing={3}>
                                <Grid item xs={12} sm={6}>
                                    <TextField fullWidth name='name' label='Имя' />
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <TextField fullWidth name='email' label='E-mail' />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField fullWidth name='message' label='Комментарий' multiline rows={4}/>
                                </Grid>
                                <Grid item xs={12}>
                                    <CustomBtn text='Отправить' />
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>

                {/* Contacts info */}
                <Grid item xs={12} lg={5}>
                    <Grid container>
                        <Grid item className='section_title mb_30'>
                            <span></span>
                            <h6 className='section_title_text'>Контактная информация</h6>
                        </Grid>
                        <Grid item xs={12}>
                            <Grid container>
                                <Grid item xs={12}>
                                    <Typography className='contactInfo_item'>
                                        <span>Адрес: </span>{resumeDate.address}
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography className='contactInfo_item'>
                                        <span>Телефон: </span>{resumeDate.phone}
                                    </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography className='contactInfo_item'>
                                        <span>Email: </span>{resumeDate.email}
                                    </Typography>
                                </Grid>
                            </Grid>
                        
                            <Grid item xs={12}>
                                <Grid container className='contactInfo_socialsContainer'>
                                    {Object.keys(resumeDate.socials).map(key =>(
                                        <Grid item className='contactInfo_social'>
                                            <a href={resumeDate.socials[key].link}>{resumeDate.socials[key].icon}</a>
                                        </Grid>
                                    ))}
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </>
    )
}

export default Resume
