import React from 'react';
import { Button } from '@material-ui/core';

import './Button.css';

const CustomBtn = ({text, icon, link}) => {
    return (
        <Button className='custom_btn' endIcon={icon ? (<div className='btn_icon_container'>{icon}</div>) : null} href={link} about='_blank'>
            <span className='btn_text'>{text}</span>
        </Button>
    )
}

export default CustomBtn;
